export default data => {
  const getdata = data.filter(item => item.iso_3166_1 === 'US');
  if (getdata.length !== 0) {
    return getdata;
  } else {
    return 'N/A';
  }
  //   const ff = getdata.find(({ certification }) =>
  //     certification !== '' ? certification : 'n/a'
  //   );
};

